import ChequeStatusService from '@/services/cheque/ChequeStatusService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let ChequeStatusTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultChequeStatusTableFilter: {
      type: String,
      default: null
    },
    defaultChequeStatusTableOrderBy: {
      type: String,
      default: null
    },
    statusBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      chequeStatusTableData: [],
      chequeStatusTableFilterTimer: null,
      chequeStatusTableFilter: this.defaultChequeStatusTableFilter,
      chequeStatusTableOrderBy: this.defaultChequeStatusTableOrderBy,
      chequeStatusTableContainer: 'loading-container',
      chequeStatusTableBusy: false,
      chequeStatusTableTotalRows: 0,
      chequeStatusTableCurrentPage: 1,
      chequeStatusTablePerPage: 10
    }
  },
  computed: {
    chequeStatusTableNoDataText: function () {
      return this.chequeStatusTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    chequeStatusTableTotalPages: function () {
      return Math.ceil(this.chequeStatusTableTotalRows / this.chequeStatusTablePerPage)
    },
    chequeStatusTablePagingInfo: function () {
      return this.pagingInfo(this.chequeStatusTableCurrentPage, this.chequeStatusTablePerPage, this.chequeStatusTableTotalRows)
    }
  },
  methods: {
    loadChequeStatusTableData (loadingContainerId) {
      let self = this
      this.chequeStatusTableBusy = true
      this.chequeStatusTableContainer = loadingContainerId || this.chequeStatusTableContainer
      this.startLoading(this.chequeStatusTableContainer)
      ChequeStatusService.chequeStatuses({
        pageIndex: this.chequeStatusTableCurrentPage - 1,
        pageSize: this.chequeStatusTablePerPage,
        filter: this.chequeStatusTableFilter,
        orderByField: this.chequeStatusTableOrderBy,
        statusBehaviourEnumId: this.statusBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.chequeStatusTableContainer)
        self.chequeStatusTableBusy = false
        self.chequeStatusTableTotalRows = response.data.totalRows
        self.chequeStatusTableData = response.data.chequeStatusList || []
      }).catch(function (error) {
        self.stopLoading(self.chequeStatusTableContainer)
        self.chequeStatusTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshChequeStatusTable () {
      this.loadChequeStatusTableData()
    },
    filterChequeStatusTable (filter) {
      let self = this
      if (this.chequeStatusTableFilter !== filter) {
        clearTimeout(this.chequeStatusTableFilterTimer)
        this.chequeStatusTableFilterTimer = setTimeout(() => {
          self.chequeStatusTableFilter = filter
          self.loadChequeStatusTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.chequeStatusTableFilterTimer)
      }
    },
    orderChequeStatusTable (orderBy, active) {
      this.chequeStatusTableOrderBy = (active ? '' : '-') + orderBy
      this.loadChequeStatusTableData()
    }
  },
  watch: {
    chequeStatusTableCurrentPage: function () {
      this.refreshChequeStatusTable()
    },
    chequeStatusTablePerPage: function () {
      this.refreshChequeStatusTable()
    },
    statusBehaviourEnumId: function () {
      this.refreshChequeStatusTable()
    }
  }
}

export default ChequeStatusTableMixin
